import { useHistory } from "react-router-dom";
import { Nav, Navbar, Form, Button, FormControl } from 'react-bootstrap';

const Navigation = ({ searchText, setSearchText }) => {
  const history = useHistory();

  const updateSearchText = (e) => {
    e.preventDefault()
    history.push('/search')
    setSearchText(e.target.value)
  }

  return (
    <>
      <Navbar bg="dark" variant='dark' expand="lg">
        <Navbar.Brand id="navbar-brand" href="/">Browzie</Navbar.Brand>
        <Navbar.Toggle id="nav-icon" aria-controls="navbarScroll" />
        <Navbar.Collapse id="navbarScroll">
          <Nav
            className="mr-auto my-2 my-lg-0"
            style={{ maxHeight: '100px' }}
            navbarScroll
          >
            <Nav.Link href="/about">About</Nav.Link>
            <Nav.Link href="#" disabled>
              Coming Soon
            </Nav.Link>
          </Nav>
          <Form onSubmit={updateSearchText} className="d-flex ms-auto" id="form">
            <FormControl
              type="search"
              placeholder="Search"
              className="mr-2"
              aria-label="Search"
              value={searchText}
              onChange={updateSearchText}
            />
            <Button
              variant="outline-success"
              type="submit"
            >
              Search
            </Button>
          </Form>
        </Navbar.Collapse>
      </Navbar>
    </>
  )

};

export default Navigation;
